import axios from 'axios'
import GETEXAMDETAILLIST from '../mutation-types'

const state = {
	examDetailList:[]
}

const getters = {
	examDetailList: state => state.examDetailList
}

const actions = {
	getExamDetailList({commit , state} , param){
		axios.get('/api/examdetail/getExamDetail' , {params:param}).then((res) => {
			if (res.status === 200) {
				if (res.data.returncode === 0) {
					commit(GETEXAMDETAILLIST , res.data.result);
				}
			}
		});
	}
}

const mutations = {
	[GETEXAMDETAILLIST](state , examDetailList){
		state.examDetailList = examDetailList;
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


