import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
const Demo = resolve => require.ensure([], () => {
  resolve(require('../components/Demo'))
}, 'demo')

const HelloWorld = resolve => require.ensure([], () => {
  resolve(require('../components/HelloWorld'))
}, 'hello_world')
//日程
const Schedule = resolve => require.ensure([], () => {
  resolve(require('../components/Schedule'))
}, 'schedule')
//詳情
const ResumeDetails = resolve => require.ensure([], () => {
  resolve(require('../components/ResumeDetails'))
}, 'resumeDetails')
//添加评语
const ResumeComment = resolve => require.ensure([], () => {
  resolve(require('../components/ResumeComment'))
}, 'resumeComment')
//登录  
const Login = resolve => require.ensure([], () => {
  resolve(require('../components/Login'))
}, 'login')
//考场列表
const ExaminationRoom = resolve => require.ensure([], () => {
  resolve(require('../components/ExaminationRoom'))
}, 'examination_room')
//考场搜索
const ExaminationSearch = resolve => require.ensure([], () => {
  resolve(require('../components/ExaminationSearch'))
}, 'examination_search')
//简历列表
const ResumeList = resolve => require.ensure([], () => {
  resolve(require('../components/ResumeList'))
}, 'resumeList')
export default new Router({
  mode:"history",
  
  routes: [
    {
      meta: {
        title: 'Demo'
      },
      path: '/demo',
      name: 'Demo',
      component: Demo
    },
    {
      meta: {
        title: '首页'
      },
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      meta: {
        title: '登录'
      },
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      meta: {
        title: '考场列表'
      },
      path: '/examination_room',
      name: 'ExaminationRoom',
      component: ExaminationRoom
    },
    {
      meta: {
        title: '考场搜索'
      },
      path: '/examination_search',
      name: 'ExaminationSearch',
      component: ExaminationSearch
    },
    {
      meta: {
        title: '日常'
      },
      path: '/schedule',
      name: 'Schedule',
      component: Schedule
    },
    {
      meta: {
        title: '简历详情页'
      },
      path: '/resumedetails',
      name: 'ResumeDetails',
      component: ResumeDetails
    },
    {
      meta: {
        title: '添加评语'
      },
      path: '/resumecomment',
      name: 'ResumeComment',
      component: ResumeComment
    },
    {
      meta: {
        title: '简历列表页'
      },
      path: '/resumeList',
      name: 'ResumeList',
      component: ResumeList
    },
  ]
})



