import axios from 'axios'
import {GETEXAMDATE} from '../mutation-types'

const state = {
	examDate:[]
}
const getters = {
	examDate: state => state.examDate
}
const actions = {
	getExamDate({commit , state} , param){
		axios.get('/api//examroom/getExamDate',{params:param}).then((res)=>{
			if (res.status === 200) {
				if (res.data.returncode === 0) {
					commit(GETEXAMDATE , res.data.result.map(val => val.split('-')[1]));
				}
			}
		});
	}
}
const mutations = {
	[GETEXAMDATE](state,examDate){
		state.examDate=examDate;
	}
}
export default {
	state,
	getters,
	actions,
	mutations
}


